<template>
  <div class="error-main">
    <div v-if="pageStyle !=0" class="status">
      <div :class="getCss()" />
      <h3>{{ getTitle() }}</h3>
      <van-button v-show="showButton" type="info" @click="errorButton">{{ getButton() }}</van-button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Error',
  /*
      -1 未登录，请先登录
      0 正常状态不显示
     1 暂无数据
     2 无网络，请检查当前网络
     3 网络请求失败，请再次尝试*/
  props: {
    // pageButton: {
    //   type: String,
    //   required: true
    // },
    // pageTitle: {
    //   type: String,
    //   required: true
    // },
    // pageImage: {
    //   type: String,
    //   required: true
    // },
    pageStyle: {
      type: null,
      required: true
    },
    showButton: {
      default: true,
      type: Boolean
    }
  },
  data() {
    return {
    }
  },
  watch: { // 监听父级传来的restaurantCategoryIds，当值发生变化的时候重新获取餐馆数据，作用于排序和筛选
    pageTitle: function(value) {
      // switch (value) {
      //   case -1 :
      //     this.nologin = true
      //     return
      //   case 0 :
      //     this.nomore = true
      //     return
      //   case 1:
      //     this.nodata = true
      //     return
      //   case 2 :
      //     this.nonet = true
      //     return
      //   case 3 :
      //     this.noapache = true
      // }
    }
  },
  created() {

  },
  methods: {

    errorButton() {
      if (this.pageStyle == -1) { //eslint-disable-line
        this.$router.push('/login')
      } else {
        this.$emit('errorButton')
      }
    },

    getTitle() {
      if (this.pageStyle == -1) { //eslint-disable-line
        return '未登录，请先登录'
      } else if (this.pageStyle == 0) { //eslint-disable-line
        return '正常状态不显示'
      } else if (this.pageStyle == 1) { //eslint-disable-line
        return '暂无数据'
      } else if (this.pageStyle == 2) { //eslint-disable-line
        return '无网络，请检查当前网络'
      } else if (this.pageStyle == 3) { //eslint-disable-line
        return '网络请求失败，请再次尝试'
      }else if (this.pageStyle == 4) { //eslint-disable-line
        return this.pageTitle
      }
    },
    getCss() {
      if (this.pageStyle == -1) { //eslint-disable-line
        return 'status-icon no-result'
      } else if (this.pageStyle == 0) { //eslint-disable-line
        return 'status-icon no-network'
      } else if (this.pageStyle == 1) { //eslint-disable-line
        return 'status-icon no-result'
      } else if (this.pageStyle == 2) { //eslint-disable-line
        return 'status-icon no-network'
      } else if (this.pageStyle == 3) { //eslint-disable-line
        return 'status-icon no-network'
      }else if (this.pageStyle == 4) { //eslint-disable-line
        return 'status-icon no-xiangmu'
      }
    },
    getButton() {
      if (this.pageStyle == -1) { //eslint-disable-line
        return '登录'
      } else {
        return '再试一次'
      }
    }

  }
}

</script>

