<template>
  <div id="didaList">
    <!-- <h3 v-if="totalpageno==0&&noDataText"  class="noDataText">{{ noDataText }}</h3> -->
    <div v-if="pageStyles == 0">
      <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
        <van-list
          v-model="loading"
          :finished="finished"
          :finished-text="finishedText"
          :error-text="errorText"
          @load="onLoad"
        >
          <!-- 开放html架构 -->
          <slot />
        </van-list>
      </van-pull-refresh>
    </div>
    <div v-else>
      <error-page
        :page-style="pageStyles"
        @errorButton="parentReceive"
      />
    </div>
  </div>

</template>

<script>
import errorPage from '@/components/result-page/index'

export default {
  components: {
    errorPage
  },
  /**
   * postUrl    接口地址名
   * dataName   接口中数据参数名不传与接口地址名一样
   * reqData    传递参数对象【除pagesize,currentpage之外的参数对象】
   * noDataText 没有记录时显示的文本提示
   */
  props: {
    postUrl: {
      type: null,
      required: true
    },
    dataName: {
      type: null,
      required: true
    },
    reqData: {
      type: null,
      required: true
    },
    apptypeShow: {
      type: Boolean,
      default: true
    }
    // noDataText: {
    //   type: null,
    //   required: true
    // }
  },
  data() {
    return {
      list: [],
      pageStyles: 0, // 页面状态展示
      loading: false, // 是否在上拉加载
      finished: false, // 是否还有商品
      isLoading: false, // 是否在刷新
      query: '',
      pagesize: 10,
      xxList: [],
      currentpage: 1, // 当前页(默认从第一页开始)
      totalpageno: 0, // 总页数
      finishedText: '没有更多了',
      errorText: '请求失败，点击重新加载',
      total: 0 // 总条数
    }
  },
  created() {
    this.reqNewData = this.reqData
    console.info('reqData-----------------', this.reqData)

    const self = this

    this.$bus.$on('refList', function(req) {
      // self.text = data
      self.query = req
      self.currentpage = 1
      self.getListData(req).then(data => {
        this.loading = false
        if (this.totalpageno <= this.currentpage) { this.finished = true }
      })
    })
  },
  beforeDestroy() {
    this.$bus.$off('refList')
  },
  methods: {
    getListData() {
      const form = this.reqData
      console.info('query--11--', this.query)
      let obj = {}
      if (this.apptypeShow) {
        if (this.query != null && this.query !== '') {
          const apptype = { 'apptype': process.env.VUE_APP_Type } // 当前操作平台
          this.query = Object.assign(this.query, apptype)
        } else {
          this.query = { 'apptype': process.env.VUE_APP_Type } // 当前操作平台
        }
        obj = {
          'pageno': this.currentpage,
          'query': JSON.stringify(this.query)
        }
      } else {
        if (this.query != null && this.query !== '') {
          this.query = Object.assign(this.query)
          obj = {
            'pageno': this.currentpage,
            'query': JSON.stringify(this.query)
          }
        } else {
          obj = {
            'pageno': this.currentpage
          }
        }
      }
      console.info('form111-----------------', form)
      return this.$store
        .dispatch(this.postUrl, Object.assign(form, obj))
        .then(res => {
          // this.list.pageno = res.pageno
          this.totalpageno = res.totalpageno ? res.totalpageno : res.totalPages
          this.pageStyles = 0
          if (this.totalpageno === 0) {
            this.pageStyles = 1
          }
          if (this.currentpage === 1) {
            this.list = this.dataName ? res[this.dataName] : res
            if (this.list == null || this.list.length === 0) {
              this.pageStyles = 1
            }
            // 加载状态结束
          } else {
            if (res.length === 0) {
              this.finished = true
            } else {
              if (this.totalpageno >= this.currentpage) {
                this.list = this.list.concat(this.dataName ? res[this.dataName] : res)
              } else {
                this.finished = true
              }
            }
          }
          console.log('this.list===============================', this.list)
          if (res && res.title) {
            this.xxList.data = this.list
            this.xxList.title = res.title
            this.$emit('returnData', this.xxList)
          } else {
            this.$emit('returnData', this.list)
          }
        })
        .catch((error) => {
          this.pageStyles = 3
          this.loading = false
          this.errorText = error
          // this.pageStyles = 1
          // this.$toast.fail(error)
          // this.loading = false
        })
    },
    // 再次点击
    parentReceive() {
      this.onRefresh(this.query)
    },
    onLoad() {
      this.getListData().then(data => {
        this.loading = false
        this.currentpage++
        if (this.totalpageno < this.currentpage) { this.finished = true }
      })
    },
    onRefresh(query) {
      if (query != null && query !== '') {
        this.query = query
      } else {
        this.query = {}
      }
      // 下拉的时候第一步清空数组列表
      this.list = []
      this.currentpage = 1
      // 请求第一页数据
      this.getListData(1).then(() => {
        this.isLoading = false
        // 如果首屏不够的情况下从第二页开始继续执行onLoad()方法
        // this.currentpage++
        // 切记要将finished变为false[如果下拉加载到底不，finished变为了true，再次请求数据的时候将不会再执行下拉加载]
        this.finished = false
      })
    },
    hideLoading() {
      this.loading = false
    },
    hideIsLoading() {
      this.isLoading = false
    }
  }
}
</script>

