 -->
<template>
  <div class="spyj-page">
    <headerBar :header-data="headerData" />
    <!-- 项目信息 begin -->
    <div class="fixed-project">
      <div class="list">
        <h3>{{ dbxq.lcslmc }}</h3>
        <div class="info">
          <span>
            <i class="iconfont iconlianxiren" />
            {{ dbxq.preUsers }}
          </span>
          <span>
            <i class="iconfont iconshijian" />
            {{ dbxq.showTime }}
          </span>
          <span>
            <i class="iconfont iconpingbiaobaogao" />
            {{ dbxq.rwmc }}
          </span>
        </div>
      </div>
    </div>
    <!-- 项目信息 end -->
    <div class="container-mini">
      <div class="content">
        <div class="common-main box">
          <!-- 通用标题 beign -->
          <div class="title">
            <div class="title-left">
              <h2>选择会签人员</h2>
            </div>
          </div>
          <!-- 通用标题 end -->
          <van-cell title="会签人员" :value="hqryVal" is-link @click="showHqryPopup" />
          <div v-if="hqbqdz==='multi'">
            <van-cell title="多人设置" :value="drszVal" is-link @click="showDrszPopup" />
            <van-cell title="会签设置" :value="hqszVal" is-link @click="showHqszPopup" />
          </div>
        </div>
      </div>

      <div class="content">
        <div class="common-main box">
          <div class="txyj">
            <div class="txyj-tit">
              <span class="text-danger">*</span>
              {{ dbxq.taskCommentLabel }}
              <div class="label-text info">同意</div>
              <span class="more" @click="showCyyPopup()">选择常用语
                <i class="iconfont iconarrowright" />
              </span>
              <!--              <span class="more" @click="saveCyy()">保存常用语-->
              <!--              </span>-->
            </div>
            <div class="txyj-content">
              <textarea v-model="spyj" rows="3" :placeholder="'请输入'+dbxq.taskCommentLabel" />
            </div>
          </div>
        </div>
      </div>

      <div class="common-space" />
      <div class="foot-btn fixed">
        <ul>
          <li><van-button type="info" size="large" round @click="subimitSPYJ()">提交</van-button></li>
        </ul>
      </div>
    </div>

    <transition name="router-slid" mode="out-in">
      <router-view />
    </transition>

    <!-- 多人设置popup beign -->
    <van-popup
      v-model="showDrsz"
      position="bottom"
      round
    >
      <van-picker
        show-toolbar
        :columns="hqxxMap.drwgxList"
        value-key="value"
        @cancel="showDrsz = false"
        @confirm="drszOnConfirm"
      />
    </van-popup>
    <!-- 多人设置popup end -->

    <!-- 会签设置popup beign -->
    <van-popup
      v-model="showHqsz"
      position="bottom"
      round
    >
      <van-picker
        show-toolbar
        :columns="hqxxMap.hqszList"
        value-key="value"
        @cancel="showHqsz = false"
        @confirm="hqszOnConfirm"
      />
    </van-popup>
    <!-- 会签设置popup end -->

    <!-- 选择常用语popup beign -->
    <van-popup
      v-model="showCyy"
      position="bottom"
      round
    >
      <van-picker
        show-toolbar
        :columns="cyyList"
        value-key="nr"
        @cancel="showCyy = false"
        @confirm="xzCyyOnConfirm"
      />
    </van-popup>
    <!-- 选择常用语popup end -->

    <!-- 会签人员单选popup beign -->
    <van-popup
      v-model="showHqrySingle"
      position="bottom"
      closeable
      :style="{ height: '100%' }"
    >
      <van-index-bar :index-list="indexList">
        <div class="content">
          <van-sticky class="van-sticky-approve">
            <div class="close-button" @click="showHqrySingle = false">
              <i class="iconfont iconguanbi" />
            </div>

            <div class="select-search">
              <van-search v-model="searchVal" placeholder="请输入搜索关键词" />
              <van-button plain type="info" size="small" @click="searchRy()">搜索</van-button>
            </div>
          </van-sticky>
          <common-list
            ref="commonlist"
            class="common-list"
            :post-url="list.postUrl"
            :req-data="list.reqData"
            :data-name="list.dataName"
            @returnData="getPrizeLog($event)"
          >
            <div
              v-for="(item, xsIndex) in list.items"
              :key="xsIndex"
            >
              <div v-if="item['xs'+item.ryxs]">
                <van-index-anchor :index="item['xs'+item.ryxs]" />
              </div>
              <div v-else>
                <div class="select-renyuan" :class="{ 'active': item.isActive }" @click="selectRy(item)">
                  <div class="renyuan-m">
                    <div class="name">{{ item.xm }}</div>
                    <div class="info"><span>公司:{{ item.zzmc }}</span><span>部门:{{ item.bmmc }}</span></div>
                  </div>
                </div>
              </div>
            </div>
          </common-list>
        </div>

        <div class="common-space" />
      </van-index-bar>
    </van-popup>
    <!-- 会签人员单选popup end -->

    <!-- 会签人员多选popup beign -->
    <van-popup
      v-model="showHqryMulti"
      position="bottom"
      closeable
      :style="{ height: '100%' }"
    >
      <van-index-bar :index-list="indexList">
        <div class="content">
          <van-sticky class="van-sticky-approve">
            <div class="close-button" @click="showHqryMulti = false">
              <i class="iconfont iconguanbi" />
            </div>

            <div class="select-search">
              <van-search v-model="searchVal" placeholder="请输入搜索关键词" />
              <van-button plain type="info" size="small" @click="searchRy()">搜索</van-button>
            </div>
            <!--  已选人员 beign -->
            <div class="selected-renyuan">
              <dl>
                <dt>已选</dt>
                <dd>
                  <span
                    v-for="(yxry, yxryIndex) in yxryList"
                    :key="yxryIndex"
                    class="item"
                  >{{ yxry.xm }}</span>
                </dd>
              </dl>
            </div>
            <!--  已选人员 end -->
          </van-sticky>
          <common-list
            ref="commonlist"
            class="common-list"
            :post-url="list.postUrl"
            :req-data="list.reqData"
            :data-name="list.dataName"
            @returnData="getPrizeLog($event)"
          >
            <div
              v-for="(item, xsIndex) in list.items"
              :key="xsIndex"
            >
              <div v-if="item['xs'+item.ryxs]">
                <van-index-anchor :index="item['xs'+item.ryxs]" />
              </div>
              <div v-else>
                <div class="select-renyuan" :class="{ 'active': item.isActive }" @click="selectRy(item)">
                  <div class="renyuan-l">
                    <i class="iconfont iconxuanzhong" />
                  </div>
                  <div class="renyuan-m">
                    <div class="name">{{ item.xm }}</div>
                    <div class="info"><span>公司:{{ item.zzmc }}</span><span>部门:{{ item.bmmc }}</span></div>
                  </div>
                </div>
              </div>
            </div>
          </common-list>
        </div>

        <div class="common-space" />
        <!-- 底部按钮 begin -->
        <div class="foot-btn fixed">
          <ul>
            <li><van-button type="info" round size="large" @click="xzMutilRyOnConfirm">确定</van-button></li>
          </ul>
        </div>
        <!-- 底部按钮 end -->
      </van-index-bar>
    </van-popup>
    <!-- 会签人员多选popup end -->

  </div>
</template>

<script>
import headerBar from '@/components/header/headerBar'
import commonList from '@/components/list/commonList.vue'
import { mapState, mapMutations } from 'vuex'
import { getHqxxAction } from '../../../store/jbr/dbsx'
import { pageRouter } from '@/common/js/mixin.js'

export default {
  name: 'DbsxHq',
  components: {
    headerBar,
    commonList
  },
  mixins: [pageRouter],
  data() {
    return {
      hqxxMap: {},
      spyj: '',
      dbxq: {},
      headerData: {
        title: '待办审批',
        leftUrl: 'back',
        leftText: ' '
      },
      showHqryMulti: false,
      showHqrySingle: false,
      showHqsz: false,
      showDrsz: false,
      hqszVal: '会签设置',
      drszVal: '多人设置',
      hqryVal: '选择会签人员',
      hqryId: '',
      hqszKey: '',
      drszKey: '',
      hqbqdz: '',
      showCyy: false,
      cyyList: [],
      pageno: 1, // 页
      totalpageno: 0, // 总页数
      indexList: [],
      list: {
        items: [],
        error: false, // 是否在刷新
        postUrl: '',
        reqData: '',
        dataName: '',
        noDataText: ''
      },
      searchVal: '',
      yxryList: [],
      btnMc: '会签'
    }
  },
  computed: {
    ...mapState({
      userInfo: state => state.mh.mhUserInfo,
      // listItem: state => state.jbrdbsx.listItem,
      txBizid: state => state.jbrdbsx.txBizid
    })

  },
  created() {
    this.btnMc = this.$route.params.btnMc
    this.getHqxx()
    this.getDbxq()
    this.getcyylist()
    this.getrylist()
  },
  methods: {
    ...mapMutations([
      'jbrdbsx/SET_DBSX_CYY',
      'jbrdbsx/SET_Refresh'
    ]),
    getDbxq() {
      this.$store.dispatch('jbrdbsx/getDbxqAction', {
        bizid: this.txBizid // 单点登录传过来的执行人id
      }).then(res => {
        this.dbxq = res
      }).catch(() => {
      })
    },
    getPrizeLog(data) {
      this.list.items = data
      for (let i = 0; i < this.list.items.length; i++) {
        const ryxs = this.list.items[i].ryxs
        if (this.list.items[i]['xs' + ryxs] && this.indexList && this.indexList.indexOf(this.list.items[i]['xs' + ryxs]) === -1) {
          this.indexList.push(this.list.items[i]['xs' + ryxs])
        }
        if (this.hqxxMap.hqryid && this.hqxxMap.hqryid.indexOf(this.list.items[i].id) !== -1) {
          this.list.items[i].isActive = true
          this.yxryList.push(this.list.items[i])
        }
        for (let j = 0; j < this.yxryList.length; j++) {
          if (this.list.items[i].id === this.yxryList[j].id) {
            this.list.items[i].isActive = true
          }
        }
      }
    },
    searchRy() {
      this.indexList = []
      const query = {
        rymc: this.searchVal
      }
      this.$refs.commonlist.onRefresh(query)
      // this.$bus.$emit('refList', this.searchVal)
      // this.$refs.commlist.testNum(9809)
    },
    getrylist() {
      this.list.postUrl = 'jbrdbsx/getRyListAction'
      this.list.reqData = { dqbmid: this.userInfo.bmid }
      this.list.dataName = 'ryList'
      this.list.noDataText = '暂无可选人员'
    },
    getcyylist() {
      this.$store.dispatch('jbrdbsx/getCyyListAction', {
        pageno: this.pageno,
        id: this.userInfo.id
      }).then(res => {
        this.cyyList = res.data
        this.pageno = res.pageno
        this.totalpageno = res.totalpageno
      }).catch(() => {
      })
    },
    saveCyy() {
      if (this.spyj != null && this.spyj.length !== 0) {
        this.$store.dispatch('jbrdbsx/saveCyyAction', {
          nr: this.spyj,
          ryid: this.userInfo.id
        }).then(res => {
          this.$toast('保存成功！')
          this.getcyylist()
        }).catch(() => {
        })
      } else {
        this.$toast(this.dbxq.taskCommentLabel + '为空，不能保存为常用语!')
      }
    },
    xzCyyOnConfirm(value) {
      this.spyj = value.nr
      this.showCyy = false
    },
    getHqxx() {
      getHqxxAction({
        id: this.txBizid
      }).then(res => {
        this.hqxxMap = res
        this.hqbqdz = this.hqxxMap.hqbqdz
        if (this.hqxxMap.hqryid) {
          this.hqryId = this.hqxxMap.hqryid
        }
        if (this.hqxxMap.hqrymc) {
          this.hqryVal = this.hqxxMap.hqrymc
        }
        if (this.hqxxMap.hqsz) {
          this.hqszVal = this.hqxxMap.hqszMap[this.hqxxMap.hqsz]
          this.hqszKey = this.hqxxMap.hqsz
        }
        if (this.hqxxMap.drwgx) {
          this.drszVal = this.hqxxMap.drwgxMap[this.hqxxMap.drwgx]
          this.drszKey = this.hqxxMap.drwgx
        }
      })
    },
    drszOnConfirm(value) {
      this.drszKey = value.key
      this.drszVal = value.value
      this.showDrsz = false
    },
    hqszOnConfirm(value) {
      this.hqszKey = value.key
      this.hqszVal = value.value
      this.showHqsz = false
    },
    subimitSPYJ() {
      if (this.spyj != null && this.spyj.length > 0 && this.hqryId !== '') {
        this.$store.dispatch('jbrdbsx/sendHq', {
          id: this.txBizid,
          taskComment: this.spyj,
          hqsz: this.hqszKey,
          hqryids: this.hqryId,
          drwgx: this.drszKey,
          btnMc: this.btnMc
          // eslint-disable-next-line no-unused-vars
        }).then(res => {
          this.$toast('提交成功！')
          setTimeout(() => {
            this.$router.push('/DbsxIndex')
            this.SET_Refresh('true')
          }, 2000)
        }).catch(error => {
          // this.loading = false
          // this.pageStyles = 3
          this.$toast(error)
        })
      } else if (this.hqryId === '') {
        this.$toast('请选择人员')
      } else {
        this.$toast('请设置审批意见')
      }
    },
    selectRy(ry) {
      if (this.hqbqdz === 'multi') {
        if (ry.isActive) {
          ry.isActive = false
          const index = this.yxryList.findIndex(item => {
            if (item.id === ry.id) {
              return true
            }
          })
          this.yxryList.splice(index, 1)
        } else {
          ry.isActive = true
          this.yxryList.push(ry)
        }
      } else {
        this.hqryVal = ry.xm
        this.hqryId = ry.id
        this.showHqrySingle = false
      }
    },
    xzMutilRyOnConfirm() {
      let xzActorsId = ''
      let xzActorsVal = ''
      const yxrylist = this.yxryList
      if (yxrylist) {
        for (let i = 0; i < yxrylist.length; i++) {
          if (i === yxrylist.length - 1) {
            xzActorsId += yxrylist[i].id
            xzActorsVal += yxrylist[i].xm
          } else {
            xzActorsId += yxrylist[i].id + ','
            xzActorsVal += yxrylist[i].xm + ','
          }
        }
      }
      this.hqryId = xzActorsId
      this.hqryVal = xzActorsVal
      this.showHqryMulti = false
    },
    showCyyPopup() {
      this.showCyy = true
    },
    showHqszPopup() {
      this.showHqsz = true
    },
    showDrszPopup() {
      this.showDrsz = true
    },
    showHqryPopup() {
      if (this.hqbqdz === 'multi') {
        this.showHqryMulti = true
      } else {
        this.showHqrySingle = true
      }
    }
  }

}

</script>

<style lang="css" scoped>
    .nav-bar{box-shadow: none;}
    .page-list{padding:0 12px;}
    .van-index-bar .content{padding:0 20px 0 10px;}
    .van-index-anchor--sticky{left: 20px;}
</style>
